.animated-product-banner-two-main-container{
    width: 100vw;
    height: 100vh;
    background-image: url("./bannerTwo.jpg");
    background-size: 100% 100%;
    background-repeat: no-repeat;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 5vh 10vw;
    gap: 3vh;
}

.animated-product-banner-two-main-container h1{
    color: white;
    text-align: center;
}

.animated-product-banner-two-main-container h3{
    text-align: center;
    color: white;
    line-height: 2;
}